  line-height: 1.8;
  color: #666666; }

a {
  color: #222222;
  text-decoration: none;
  -webkit-transition: .4s;
  transition: .4s; }
  a:hover, a:focus {
    color: #ff2c57;
    text-decoration: none; }

img {
