.ptb-80 {
  padding-bottom: 80px;
  padding-top: 80px; }

.d-table {
  width: 100%;
  height: 100%; }
  .d-table-cell {
    vertical-align: middle; }

.bg-f6f6f6 {
  background: #f6f6f6; }

/*Btn Btn-Primary*/
.btn {
  padding: 9px 25px;
  font-size: 16px;
  border-radius: 30px;
  -webkit-transition: .4s;
  transition: .4s; }

.btn-primary {
  color: #fff;
  background-color: #ff2c57;
  border-color: #ff2c57; }
  .btn-primary:hover, .btn-primary:focus, .btn-primary.disabled:hover, .btn-primary.disabled:focus {
    color: #ff2c57;
    background-color: #fff;
    border-color: #ff2c57; }
  .btn-primary:not(:disabled):not(.disabled).active, .btn-primary:not(:disabled):not(.disabled):active {
    color: #fff;
    background-color: #ff2c57;
    border-color: #ff2c57; }
  .btn-primary:not(:disabled):not(.disabled).active:focus, .btn-primary:not(:disabled):not(.disabled):active:focus {
    box-shadow: none; }
  .btn-primary.focus, .btn-primary:focus {
    box-shadow: none; }
  .btn-primary.disabled, .btn-primary:disabled {
    color: #fff;
    background-color: #ff2c57;
    border-color: #ff2c57;
    opacity: 1; }

/* Section Title */
.section-title {
  margin-bottom: 60px;
  padding-left: 30px;
  position: relative; }
  .section-title::before, .section-title::after {
    content: '';
    position: absolute;
    width: 3px;
    height: 100%;
    left: 10px;
    top: 0;
    background: #ff2c57;
    -webkit-transition: .4s;
    transition: .4s; }
  .section-title::after {
    left: 0;
    height: 50%; }
  .section-title:hover::before {
    height: 50%; }
  .section-title:hover::after {
    height: 100%; }
  .section-title h2 {
    font-size: 35px;
    font-weight: 200;
    margin-bottom: 20px; }
    .section-title h2 span {
      color: #ff2c57;
      font-weight: 600; }
  .section-title p {
    max-width: 635px;
    margin-top: -7px; }
  .section-title h4 {
    font-size: 18px;
    font-weight: 300;
    color: #ff2c57;
    margin-bottom: 17px;
